@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
* {
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
  reveal.js does not work properly unless you add the following styles
  reveal.js takes width and height from parent element. If you want to use
  reveal.js inside an element, you have set width and height of the element
  before.
*/

#root {
  width: 100%;
  height: 100%;
}

.App {
  width: 100%;
  height: 100%;
}

/*
  reveal.js global setting
*/

/* give full access to positioning when disableLayout option is true */
.reveal .slides > section {
  width: 100%;
  height: 100%;
}
#title-container {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;

  width: 60%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#title {
  font-size: 5rem;
  color: black;
  margin-bottom: 1rem;
}

#author, #date {
  font-size: 2rem;
  text-align: end;
  color: #2f2f2f;
}
.intro-container {
  width: 100%;  
  height: 100%;
  display: grid;
  grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);

  /* without this property, items will overflow over height */
  grid-template-rows: minmax(0, 1fr);
}

.intro-explanation {
  border: 4px solid rgb(200, 200, 200);
  text-align: center;
  padding: 1.5rem;
  margin: 1rem;
  overflow-y: auto;
}

.intro-explanation-paragraph {
  text-indent: 2rem;
  font-size: 1.0rem;
  line-height: 2rem;
  text-align: left;
}

.intro-explanation-history {
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  justify-content: center;
  align-items: flex-start;

  border: 1px solid rgb(200, 200, 200);
  padding: 4px;
}

.intro-explanation-history-card {
  margin-right: 0.5rem;
  width: 20%;
  height: 220px;
  object-fit: cover;
}

.intro-explanation-history-card img {
  height: 200px;
}

.intro-explanation-help {
  margin: 0.4rem;
  font-size: 1.3rem;
  font-weight: bold;
  text-align: center;
}


.intro-examples {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 1rem;
}

.intro-example {
  object-fit: contain;
  width: 45%;
  margin: 0.3rem;
  transition: all 500ms ease-out;
  border-radius: 15px;
}

.intro-example p {
  margin-top: 0.3rem;
}

.intro-example:hover {
  box-shadow: 3px 3px 10px rgba(0,0,0,0.5), -3px -3px 10px rgba(0,0,0,0.5);
  cursor: pointer;
}

.video-background {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 3;
  background-color: rgba(25,25,25,0.50);
}

.video-iframe, .content-div {
  z-index: 4 !important;
  border: none;
}
.card-container {
  box-shadow: none !important;
}

.card-image {
  display: block;
  width: 100% !important;
  margin: 0 !important;
}
.squidgame-container {
  width: 100%;
  height: 100%;
  padding: 1rem;
}

.netflix-ceo {
  float: left;
  width: 40%;
  border-radius: 15px;
  margin-right: 1.0rem;
}

.squidgame-explanation {
  text-indent: 2rem;
  margin: 0.5rem;
  font-size: 1.0rem;
  line-height: 2rem;
  text-align: left;
}

.streaming-video-graph {
  clear: both;
  float: left;
  margin-right: 1.0rem;
  width: 40%;
}

